<template>
  <div id='box'>
    <div class="count_num">租借中( {{count}} )</div>
    <div class="list" v-if="list.length">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad">

        <div class="item" v-for="(item,i) in list" :key="i">
          <div class="top pos">
              <div class="left">
                <img src="@/assets/images/my/1.png" alt="" class="img">
                <span>{{item.pay_time}}</span>
              </div>
              <span :class="item.status=='0'?'light':'type'">{{item.status=="0"?"已完成":'使用中'}}</span>
          </div>
          <div class="bottom">
            <div>
              <p class="num"><i style="color:#15BDDB;">•&nbsp;</i> 酒店名称：{{item.hotel_name}}</p>
              <p class="num"><i style="color:#15BDDB;">•&nbsp;</i> 机器编号：{{item.number}}</p>
              <p><i style="color:#15BDDB;">•&nbsp;</i> 使用时长：{{item.use_time}}小时</p>
            </div>
            <div class="right" :class="item.status=='0'?'light':'type'"><span class="fh">￥</span>{{item.pay_price}}</div>
          </div>
        </div>
            
      </van-list>
    </div>
    <div style="color:#aaa;text-align:center;margin-top:20px" v-if="list.length==0">~ 暂无数据 ~</div>
    <!-- <div style="color:#aaa;text-align:center;margin-top:20px" v-else>~ 已经到底了 ~</div> -->
  </div>
</template>

<script>
import { getlease_list } from '@/axios/getData'
import { List } from 'vant'
export default {
  components: {
    vanList: List
  },
  props: {
    
  },
  data() {
    return {
      count:0,  //  租借中总数
      list:[],  //  租借中总数
      finished:false,
      loading:false,
      page:1
    };
  },
  computed: {

  },
  created() {
    this.onLoad();
  },
  methods: {
    async onLoad(){
      var orderarr = JSON.parse(localStorage.getItem('orderarr'));
      // if(this.loading){
        console.log(1)
        var res = await getlease_list({page:this.page,orderarr:orderarr.join(',')});
        // var res = await gettotal_order_list({arras:arrs.join(','), page:this.page});
        console.log(res.data);
        this.count = res.data.count
        if(this.page ==1){
          this.list = res.data.list
        } else {
          this.list = this.list.concat(res.data.list)
        }
        this.loading = false;
        this.page++
        if(this.list.length>=this.count){
          this.finished = true
        }
      // }
    }
  },
  mounted() {
    // this.$router.meta.title="10086"
  },
};
</script>

<style scoped lang="scss">
#box{
  height:100vh;
  background: #FAFAFA;
}
  .list{
    padding:20pt;
    .item{
      margin-bottom:50px;
      background: #FFF;
      border-radius: 6pt;
      box-shadow:0px 0px 10px rgba(167, 165, 165, 0.5);
      .top{
        padding:5%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .img{
          margin-right:10pt;
          width:15pt;
          height:15pt;
        }

        .left{
          display: flex;
          align-items: center;
        }
      }
      .pos{
        position: relative;
      }
      .pos::after{
        position: absolute;
        display: block;
        content:"";
        left:5%;
        width:90%;
        bottom:2pt;
        border-bottom:1pt dashed #D7D7D7;
      }
      .bottom{
        padding:5%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .right{
          font-weight: 600;
          font-size: 18pt;
          width:200px;
          .fh{
            font-size: 9pt;
            font-weight: 800;
          }
        }
      }
    }
  }
  /* 使用中 */
.type{
  color:#25E98A;
}
.light{
  color:#999;
}
.num{
  display: inline-block;
  padding-bottom:5pt;
}
.count_num{
  text-align: center;
  padding:25px;
  color:#333;
  font-weight: 600;
  font-size: 26px;
}
</style>



